 [class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;
 
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
 
  /* For safety - reset parent styles, that can break glyph codes*/
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
 
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
 
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;
 
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
 
  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}
 
.icon-ok-circled:before { content: '\E800'; } /* '' */
.icon-puzzle:before { content: '\E801'; } /* '' */
.icon-spin2:before { content: '\E831'; } /* '' */
.app{
  min-height: 100vh;
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction:column;
  flex-wrap: wrap;
  font-size: 85%;

}
nav,section{
  flex-basis: 100%;
}

@media only screen and (min-width: 900px) {
  .app{
    flex-direction: row;
  }
  nav{
    flex-basis: 40%;
  }
  section{
    flex-basis: 60%;
  }
}

.icon-spin2{
  display: none;
  position: absolute;
  left: 50%;
  top: 50%;
  color: white;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}
@-webkit-keyframes spin{
  from{
      -webkit-transform: translate(-50%,-50%) rotate(0deg);
              transform: translate(-50%,-50%) rotate(0deg);
  }
  to{
      -webkit-transform: translate(-50%,-50%) rotate(360deg);
              transform: translate(-50%,-50%) rotate(360deg);
  }
}
@keyframes spin{
  from{
      -webkit-transform: translate(-50%,-50%) rotate(0deg);
              transform: translate(-50%,-50%) rotate(0deg);
  }
  to{
      -webkit-transform: translate(-50%,-50%) rotate(360deg);
              transform: translate(-50%,-50%) rotate(360deg);
  }
}

.navigation {
    height: 100vh;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    font-size: 2em;
    list-style: none;
}

.navigation.hide li{
    display: none;
}
.navigation.hide{
    flex-direction: row;
}
.navigation.hide li{
        flex-basis: 50%;
        height: 70px;
}
.navigation.hide .mobileNavHide{
    display: block;
}
.navigation.hide{
height: 75px;
}
.navigation.hide .navArrow.mobileNavHide img{
    -webkit-transform: rotate(-90deg) translate(45%,0%);
            transform: rotate(-90deg) translate(45%,0%);
}

.navigation li{
    width: 50%;
    flex-basis: 14.28%;
    text-align: left;
    border: 1px black solid;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    
}
.navigation li a:after{
    content: "";
    background-image: url(/Quotes/static/media/active.f445bdc2.png);
    background-repeat: no-repeat;
    background-size: contain;
    opacity: 0.5;
    top: -2vh;
    left: -14px;
    bottom: 0;
    right: 0;
    position: absolute;
   display: none;
}
.navigation li a:hover:after{
    display: block;
  }
  .navigation li.dark a:hover:after{
    display: none;
  }
  .navigation li a.active:after{
    display:block
}
.navigation li.dark a.active:after{
    display:none;
}

.navigation li.dark{
    background-color: rgb(0, 0, 0);
}
.navigation li.dark:hover a{
    background-color: white;
    color: black;
}
.navigation li.dark a,.navigation li.dark span{
color: white;
}
.navigation li.hide span,.navigation li.hide a{
    color: transparent;
    font-size: 0.5em;
}
.navigation a,.navigation span{
    display: block;
    width: 100%;
    height: 100%;
    padding: 15px 5px 15px 10px;

    color: black;
    text-decoration: none;
    transition: 0.3s;
}
.navigation span{
    background-color: transparent;
}
.navArrow span{
    position: relative;
    height: 100%;
    overflow: hidden;
}
.navArrow span img{
    position: absolute;
    max-height: 90%;
    top: 50%;
    left: 40%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
   
}

@media only screen and (min-width: 900px) {
    .navigation.hide{
        flex-direction: column;
        height: 100vh;
    }
    .navigation.hide li{
        display: block;
        height: auto;
        flex-basis: 14.28%;
    }
    .navigation.hide .navArrow.mobileNavHide img{
        -webkit-transform: translate(-50%,-50%);
                transform: translate(-50%,-50%);
    }
    .navigation a,.navigation span{
        padding: 25px 15px 15px 30px;
    }
    
}
.content{
    width: calc(100% - 26px);
    min-height: calc(100vh - 140px);
    border: 2px rgb(0, 0, 0) solid;
    margin: 13px;
    padding: 20px;
}
.content h1,.content h2,.content span{
position: relative;
font-size: 1.5rem;
font-weight: bold;
margin-bottom: 25px;
}
.content h1::after,.content h2::after,.content span::after{
    content: '';
    position: absolute;
    width: 75%;
    height: 3px;
    bottom: -8px;
    left: -5px;
    background-color: black;
}
.content h2::after{
    width: 60%;
}
.content span{
    display: block;
    text-align: center;
}
.content span::after{
    width: 200px;
    left: calc(50% - 100px);
}
.content article{
    margin: 30px 40px 10px 40px;
    padding: 13px;
    text-align: justify;
    letter-spacing: 2px;
    line-height: 22px;
    color: white;
    background-color: rgb(90, 90, 90);
    border: 3px rgb(179, 179, 179) solid;
    position: relative;
    z-index: 1;
}
.content article::after,.content article::before{
   content: '';
   position: absolute;
    width: 25px;
    height: 70%;
    background-color:  rgb(90, 90, 90);
    border: 3px rgb(179, 179, 179) solid;
   
    top: 13%;
    right: -31px;
    z-index: -1;
}
.content article::after{
    border-left: 3px solid rgb(90, 90, 90);
}
.content article::before{
    left: -31px;
    border-right: 3px solid rgb(90, 90, 90);
}
.content a{
    color: white;
    font-weight: bold;
}
.footer{
    text-align: center;
}
.footer span{
    position: relative;
}
.footer span::after, .footer span::before{
content: '';
position: absolute;
width: 10px;
height: 10px;
background-color: black;
top: 50%;
left: -14px;
-webkit-transform: translate(-50%,-50%) rotate(45deg);
        transform: translate(-50%,-50%) rotate(45deg);
}
.footer span::after{
    left: auto;
    right: -24px;
}
@media only screen and (min-width: 900px) {
    .content{
        min-height: calc(100vh - 50px);
    }
}
.drawSeciton button{
display: block;
margin: 0px auto;
padding: 7px 20px 7px 0px;
background-color: white;
border: 3px black solid;
cursor: pointer;
border-radius: 4%;
position: relative;
}

.drawSeciton button.load .icon-spin2{
/*spin animation and .icon-spin2 setting are in app.css */
    display: block;
    -webkit-animation: spin 1.4s linear infinite;
            animation: spin 1.4s linear infinite;
}
.drawSeciton button.load{
    background-color: rgb(92, 92, 92);
}
.drawSeciton i{
    font-style: normal;
    font-size: 30px;
}
.drawSeciton article::after,.drawSeciton article::before{
display: none;
}
.drawSeciton article{
    background-color:transparent; 
    border: transparent;
    color: black;
    margin: 10px 20px;
    font-size: 1.2rem;
    letter-spacing: 1px;
    text-align: center;
}
.quote{
    position: relative;
    margin: 20px 10px 30px 10px;
    z-index: 2;
}
.quote::before{
    content: '\201E';
    position: absolute;
    top: -55px;
    right: 0px;
    font-size: 300px;
    color:rgb(184, 182, 182);
    z-index: -1;
}
.quote span{
    font-size: 1.2rem;
    display: block;
    text-align: right;
    font-weight: bold;
}
.quote span::after{
display: none;
}
.quoteDescription{
    margin: 50px 0px 0px 0px;
    padding: 0px 10px;
    position: relative;
    border-right: 3px rgba(167, 167, 167, 0.473) solid;
    border-left: 3px rgba(167, 167, 167, 0.473) solid;
}

.contactForm{
    width: 85%;
    margin: 20px auto;
    text-align: center;
    font-size: 1.5rem;
}
.contactForm label{
    display: block;
    margin: 20px 0px 0px 0px; 
}
.contactForm input,.contactForm textarea,.contactForm select,.contactForm button{
    position: relative;
    padding: 7px;
    font-size: 0.7em;
    background-color: rgb(250, 250, 250);
    border: 2px black solid;
    cursor: pointer;
}
.contactForm select{
    width: 100%;
    margin-top: 7px;
}
.contactForm input{
    margin: 10px 25%;
    width: 50%;
}
.contactForm textarea{
    width: 70%;
    min-height: 150px;
    max-width: 100%;
}

.contactForm button.load{
    background-color: rgb(92, 92, 92);
}

.contactForm button.load .icon-spin2{
    /*spin animation and .icon-spin2 setting are in app.css */
    display: block;
    -webkit-animation: spin 1.4s linear infinite;
            animation: spin 1.4s linear infinite;
}

.failValidation{
    font-size: 0.55em;
    color: rgb(72, 133, 130);
    position: relative;
}
.failValidation strong{
    position: absolute;
    top: 0%;
    -webkit-transform: translate(-400%,-25%);
            transform: translate(-400%,-25%);
    font-size: 1.6rem;
    color: rgb(119, 24, 24);
}

